.app-header {
    background-color: #ff7e00;
    padding: 5px 10px;
    font-family: Arial, Helvetica, sans-serif;
    a {
        color: #F5F2EC;
        font-weight: 500;
    }
    h1 {
        font-family: Baumans;
        color: #F5F2EC;
    }
    .img-logo {
        width: 100px;
    }
    .nav-list {
        > *:not(:first-child) {
            margin-inline-start: 10px;
        }
    }
}
